.div-center {
    display: table;
    margin: 0 auto;
}

.div-center img {
    width: 10rem;
}

.buttons-group {
    display: flex;
}

.buttons-group button:first-child {
    margin-right: 8px;
}

a, div, img, p, button {
    user-select: none;
    -webkit-user-select: none;
    -webkit-touch-callout: none;
    -webkit-tap-highlight-color: transparent;
}

p {
    color: black;
}

.money {
    text-indent: 10px;
    font-size: 47px;
    color: black;
    font-weight: 19px;
}

.ruble {
    font-size: 15px;
    vertical-align: 25px;
    text-indent: 4px;
}